@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .player-card {
        @apply flex flex-col items-center justify-center rounded-md text-black w-16 h-16 font-bold text-sm;
    }

    .cell {
        @apply flex items-center justify-center w-16 h-16 rounded-md font-bold;
    }
        .btn {
            @apply text-black p-2 w-full rounded-md items-center font-bold items-center;
        }
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans",
            "Helvetica Neue",
            sans-serif;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
